import React from 'react';
import configureStore from 'redux-mock-store';
import {
  cleanup,
  fireEvent,
  render,
  screen,
} from '@testing-library/react-native';

import * as eva from '@eva-design/eva';
import {ApplicationProvider} from '@ui-kitten/components';
// import configureStore from '../../redux/store';
import Home from '../Home';
import {getHotels} from '../../redux/actions';
import {Provider} from 'react-redux';
import hotels_response from './fixtures/hotels_response';
import {NavigationContainer} from '@react-navigation/native';
let mockStore = configureStore();

const navigation = {
  navigate: jest.fn(),
  goBack: jest.fn(),
};

const store = mockStore({
  hotel: {
    hotels: hotels_response.result.hotels,
  },
});

const setup = () =>
  render(
    <Provider store={store}>
      <ApplicationProvider {...eva} theme={eva.light}>
        <NavigationContainer>
          <Home hotels={hotels_response.result.hotels} />
        </NavigationContainer>
      </ApplicationProvider>
    </Provider>,
  );

describe('Test homepage screen', () => {
  beforeEach(() => {
    mockStore.dispatch = jest.fn();
    setup();
  });

  afterEach(cleanup);

  it('should display the list of hotels', () => {
    expect(screen.getByTestId('search-box')).toBeDefined();
  });
  it('fetches the hotels on mount', () => {
    const expectedAction = getHotels({q: '', page: 1});
    expect(store.dispatch).toHaveBeenCalledTimes(1);
    expect(store.dispatch).toHaveBeenCalledWith(expectedAction);
    expect(store.getActions()).toContainEqual(expectedAction);
  });
});
